export default {
	name: 'uniTerm',
	props: {
		view: {
			type: String,
			default: '' //small  full
		},
		list: {
			type: Array || Object,
			default() {
				return [];
			}
		}
	},
	data() {
		return {};
	},
	methods: {
		doTerm(index,item){
			this.$emit("onTerm",{
				index:index,
				value:item
			})
		}
	}
};
